import React from "react";

export class Navbar extends React.Component{
    constructor(){
        super()
    }

    onClick(){
        const menu = document.getElementsByClassName('Navbar-mobile-menu')
        let style = 'Navbar-mobile-menu_on'

        menu[0].classList.toggle(style)
    }

    render(){
        return(
            <nav id = "Navbar">
                <div className = 'Navbar-container'>
                    <a className = 'Navbar-container-href' href = '/'>Главная</a>
                    <a className = 'Navbar-container-href' href = '#About'>О нас</a>
                    <a className = 'Navbar-container-logo' href = '/'>Coffee Point</a>
                    <a className = 'Navbar-container-href' href = '#Menu'>Меню</a>
                    <a className = 'Navbar-container-href' href = '#Contacts'>Контакты</a>
                </div>
                <div className = 'Navbar-mobile'>
                    <a className = 'Navbar-container-logo' href = '/'>Coffee Point</a>
                    <a onClick = {this.onClick = this.onClick.bind(this)} className = 'Navbar-mobile-button'>
                        <span className = 'Navbar-mobile-button-line' />
                        <span className = 'Navbar-mobile-button-line' />
                        <span className = 'Navbar-mobile-button-line' />
                    </a>
                    <div className = 'Navbar-mobile-menu'>    
                        <a className = 'Navbar-mobile-menu-href' href = '/'>Главная</a>
                        <a onClick = {this.onClick = this.onClick.bind(this)} className = 'Navbar-mobile-menu-href' href = '#About'>О нас</a>
                        <a onClick = {this.onClick = this.onClick.bind(this)} className = 'Navbar-mobile-menu-href' href = '#Menu'>Меню</a>
                        <a onClick = {this.onClick = this.onClick.bind(this)} className = 'Navbar-mobile-menu-href' href = '#Contacts'>Контакты</a>
                    </div>
                </div>

            </nav>
        )
    }
}